import { Injectable } from "@angular/core";
import { Http, Headers, RequestOptions, Response } from "@angular/http";
import { Observable } from "rxjs/Rx";
import "rxjs/add/operator/do";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { environment } from "../environments/environment";

@Injectable()
export class LookupService {
    private _locaties: string[] = [];

    constructor(private _http: Http) {
        //
    }

    // ReSharper disable once TsResolvedFromInaccessibleModule
    getLocaties(): Observable<string[]> {
        
        return this._http.get(`${environment.serviceUrl}/api/lookup/getlocaties`)
            .map((response: Response) => response.json() as string[])
            .catch(this.handleError);
    }

    // ReSharper disable once TsResolvedFromInaccessibleModule
    getKentekens(): Observable<string[]> {
        return this._http.get(`${environment.serviceUrl}/api/lookup/getkentekens`)
            .map((response: Response) => response.json() as string[])
            .catch(this.handleError);
    }

    private handleError(error: Response) {
        alert(error);
        //console.error(error);
        return Observable.throw(error.json().error || "Server error");
    }
}


