export class Rit {
    id: number;
    datum: Date;
    kenteken: string;
    tijdStart: Date;
    locatieStart: string;
    kmStart: number;
    tijdEind: Date;
    locatieEind: string;
    kmEind: number;

    constructor(values: Object = {}) {
        Object.assign(this, values);
    }
}



