import {Component, OnInit} from "@angular/core";
import {Rit} from "../rit";
import {RitService} from "../rit.service";

@Component({
    selector: "app-rit-list",
    templateUrl: "./rit-app-list.component.html",
    styleUrls: ["./rit-app-list.component.css"]
})
export class RitAppListComponent implements OnInit {
    private _ritten: Rit[] = [];
    isRequesting = false;

    constructor(private _ritService: RitService) {
        //
    }

    ngOnInit(): void {
        this.isRequesting = true;
        this._ritService.getAllRitten()
            .subscribe((ritten) => {
                    this._ritten = ritten;
                    this.isRequesting = false;
                },
                error => this.handleError(error));
    }

    get ritten(): Rit[] {
        return this._ritten;
    }

    private handleError(error: string) {
        this.isRequesting = false;
        //console.error(error);
        alert(error);
    }
}


