import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { RouterModule, Routes } from "@angular/router";

import { AppComponent } from "./app.component";
import { RitAppComponent } from "./rit-app/rit-app.component";
import { DatepickerModule, TimepickerModule } from "ng2-bootstrap";
import { RitAppListComponent } from "./rit-app-list/rit-app-list.component";
import { RitAppFormComponent } from "./rit-app-form/rit-app-form.component";
import { SpinnerComponent } from "./spinner/spinner.component";

const appRoutes: Routes = [
    { path: "edit/:id", component: RitAppFormComponent },
    { path: "new", component: RitAppFormComponent },
    { path: "", component: RitAppListComponent }
];

@NgModule({
    declarations: [
        AppComponent,
        RitAppComponent,
        RitAppListComponent,
        RitAppFormComponent,
        SpinnerComponent
    ],
    imports: [
        BrowserModule,
        FormsModule,
        HttpModule,
        DatepickerModule.forRoot(),
        TimepickerModule.forRoot(),
        RouterModule.forRoot(appRoutes)
    ],
    providers: [],
    bootstrap: [AppComponent]
})
export class AppModule {
    //
}


