import { Component, OnInit } from "@angular/core";
import { Rit } from "../rit";
import { RitService } from "../rit.service";
import { LookupService as LookupService } from "../lookup.service";
import { Router, ActivatedRoute, Params } from "@angular/router";
import { Observable } from "rxjs/Rx";
import "rxjs/add/operator/map";

@Component({
    selector: "app-rit-form",
    templateUrl: "./rit-app-form.component.html",
    styleUrls: ["./rit-app-form.component.css"]
})
export class RitAppFormComponent implements OnInit {
    private _rit = new Rit();
    private _locaties: string[] = [];
    private _kentekens: string[] = [];
    private _isNew: boolean;
    isRequesting = false;

    constructor(
        private _route: ActivatedRoute,
        private _router: Router,
        private _ritService: RitService,
        private _lookupService: LookupService) {
        //
    }

    ngOnInit(): void {
        this.isRequesting = true;
        Observable.forkJoin(this.loadLocaties(), this.loadKentekens())
            .subscribe(() => {
                    this.loadRit();
                },
                error => {
                    this.handleError(error);
                });
    }

    submitForm() {
        this.isRequesting = true;
        this._ritService.updateRit(this._rit)
            .subscribe((rit) => {
                    this._rit = rit;
                    this.isRequesting = false;
                    this._router.navigate([""]);
                },
                error => this.handleError(error));
    }

    get isNew(): boolean {
        return this._isNew;
    }

    get rit(): Rit {
        return this._rit;
    }

    get locaties(): string[] {
        return this._locaties;
    }

    get kentekens(): string[] {
        return this._kentekens;
    }

    private loadRit() {
        this._route.params.map(p => p["id"])
            .subscribe((sid) => {
                var id = +sid;
                if (!id) {
                    // if id is NOT found, populate form with empty rit.
                    this._isNew = true;
                    this.isRequesting = false;
                    this._rit = new Rit({
                        datum: new Date(),
                        tijdStart: new Date(),
                        tijdEind: new Date()
                    });
                } else {
                    // otherwise, download rit data and display in form.
                    this._isNew = false;
                    this._ritService.getRitById(id)
                        .subscribe((rit) => {
                                this.isRequesting = false;
                                this._rit = rit;
                            },
                            error => this.handleError(error));
                }
            });
    }

    private loadLocaties(): Observable<string[]> {
        return this._lookupService.getLocaties()
            .do((locaties: string[]) => { this._locaties = locaties; });
    }

    private loadKentekens(): Observable<string[]> {
        return this._lookupService.getKentekens()
            .do((kentekens: string[]) => { this._kentekens = kentekens; });
    }

    private handleError(error: string) {
        this.isRequesting = false;
        //console.error(error);
        alert(error);
    }
}


