import {Injectable} from "@angular/core";
import {Rit} from "./rit";
import {Http, Headers, RequestOptions, Response} from "@angular/http";
import {Observable} from "rxjs/Rx";
import "rxjs/add/operator/do";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { environment } from "../environments/environment";

@Injectable()
export class RitService {
    private _ritten: Rit[] = [];
    private _index = 0;

    constructor(private _http: Http) {
        //
    }

    updateRit(rit: Rit): Observable<Rit> {
        const headers = new Headers({ "Content-Type": "application/json" });
        const options = new RequestOptions({ headers: headers });
        return this._http.post(`${environment.serviceUrl}/api/rit/update`, rit, options)
            .map((response: Response) => response.json() as Rit)
            .catch(this.handleError);
    }

    getAllRitten(): Observable<Rit[]> {
        return this._http.get(`${environment.serviceUrl}/api/rit/getall`)
            .map((response: Response) => response.json() as Rit[])
            .catch(this.handleError);
    }

    getRitById(id: number): Observable<Rit> {
        return this._http.get(`${environment.serviceUrl}/api/rit/getbyid/${id}`)
            .map((response: Response) => response.json() as Rit)
            .catch(this.handleError);
    }

    private handleError(error: Response) {
        //console.error(error);
        alert(error);
        return Observable.throw(error.json().error || "Server error");
    }
}


